
const postData = async (url, data) => { // async  await   
    const res = await fetch(url, { //  await   ,     
        method: "POST", //    ( await)  res    ,   (     )   ,    
        headers: {
            'Content-type': 'application/json'
        },
        body: data
    });

    return await res.json();
};

const getResourses = async (url) => { // async  await   
    const res = await fetch(url);

    if (!res.ok) {
        throw new Error(`Could not fetch ${url}, status ${res.status}`);
    }

    return await res.json();
};

export {postData};
export {getResourses};